    justify-content: space-between;
}






.product_box {
    width: 280px;
    border: 1px solid #ddd;
    margin-bottom: 40px;
    overflow: hidden;
    transition: 0.3s;
}
.product_box:hover {
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
}

.product_box .main_poto {
    border-bottom: 1px solid #ddd;
    height: 280px;
    position: relative;
}
.product_box .main_poto img {
    /* width: 100%; */
    width: 280px;
}

.product_box .main_poto .btnBox {
    position: absolute;
    top: 0;
    left: 0;
    width: 280px;
    height: 280px;
    background-color: #808080b3;
    border-radius: none;
    opacity: 0;
    transition: 0.3s;
}
.product_box:hover .main_poto .btnBox {
    opacity: 1;
    border-radius: 1px;
}
.product_box .main_poto .btnBox .icon {
    width: 40px;
    height: 40px;
    margin-top: 100px;
    padding: 10px;
    color: white;
    opacity: 0.6;
    border-bottom: 4px solid white;
}
.product_box .main_poto .btnBox .icon:hover {
    opacity: 1;
}
.product_box .main_poto .btnBox .heart_icon {
    margin-right: 20px;
}



.product_box .text_box {
    padding: 15px;
    box-sizing: border-box;
}

