    margin: 0 auto;
}

.product_box .text_box .price {
    margin-top: 10px;
    font-size: 16px;
}
.product_box .text_box .price span {
    font-size: 14px;
}

.product_box .text_box .logo {
    display: flex;
    justify-content: center;
}
.product_box .text_box .logo img {
    width: 30px;
    height: 30px;
    border-radius: 15px;
    margin-right: 7px;
    border: 1px solid #ddd;
}
.product_box .text_box .logo p {
    line-height: 30px;
}

.bestBtn {
    margin-top: 30px;
}
